.process {
  // max-width: 46.25vw;
  // margin-bottom: 13.6rem;
  // background-color: #fcfcfd;
  padding-top: 160px;

  @media screen and (max-width: 1200px) {
    // 1.33
    max-width: 100%;
  }

  &__name {
    display: flex;
    align-items: center;
    margin-bottom: 19px;
  }

  &__icon {
    width: 21px;
    height: 21px;
    margin-right: 16px;

    // &-img {
    //   width: 100%;
    //   height: 100%;
    // }
  }

  &__text {
    font-weight: 800;
    font-size: 16px;
    color: $color-primary;
    text-transform: uppercase;

    &--color-white {
      color: #fff;
    }
  }

  &__title {
    font-weight: 900;
    font-size: 32px;
    line-height: 1.4;
    max-width: 362px;
    margin-bottom: 64px;
  }

  &__paragraph {
    font-size: 16px;
    line-height: 150%;
  }

  &__card {
    width: 240px;
    height: 256px;
    // background-color: red;
    padding: 48px 32px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    flex-direction: column;
    text-align: center;
    background-color: #fff;

    &--mr {
      margin-right: 58px;
    }

    &--mt {
      margin-top: 112px;
    }
  }

  &__circle {
    width: 48px;
    height: 48px;
    background: #f1fbfe;
    border-radius: 50%;
    position: relative;
    opacity: 1;
    margin-bottom: 32px;
    // margin: auto;
  }

  &__circle-number {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%);
    font-weight: 900;
    font-size: 40px;
    line-height: 1;
    color: #56ccf2;
    opacity: 1;
  }

  &__desc {
    font-size: 12.6px;
  }

  &__main {
    display: flex;
    // justify-content: space-between;
  }

  &__left {
    flex-basis: 362px;
    flex-shrink: 0;
  }

  &__right {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
  }
}
