.slider {
  position: relative;
  width: 100%;
  text-align: center;
  margin-bottom: 16.4rem;

  // @media screen and (max-width: 1440px) {
  //   max-width: 108rem;
  // }

  // @media screen and (max-width: 1080px) {
  //   margin-bottom: 10.5rem;
  // }

  &__img {
    width: 100%;
  }

  &__button {
    position: absolute;
    left: -8rem;
    top: 50%;
    transform: translate(0, -50%);
    width: 8rem;
    height: 6rem;
    background-color: $header-btn-bcg;
    color: $color-txt-white;
    font-size: 1.6rem;
    border: none;
    cursor: pointer;

    &:active {
      height: 5.5rem;
    }

    &:hover {
      background-color: darken($color: $color-secondary, $amount: 4);
    }

    @media screen and (max-width: 1080px) {
      position: initial;
    }

    &--right {
      margin-left: 8rem;
      border-top-right-radius: 50px;
      border-bottom-right-radius: 50px;

      @media screen and (max-width: 1080px) {
        margin-left: 0;
      }
    }

    &--left {
      border-top-left-radius: 50px;
      border-bottom-left-radius: 50px;
    }
  }
}
